/* General Layout and Body */
body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace; /* Retro, typewriter vibe */
  background-color: #ffffff; /* Pitch black background */
  color: #000000; /* Dark red text */
  text-align: center;
  box-sizing: border-box;
}

h1, h2, h3 {
  font-family: 'Courier New', Courier, monospace;
  text-transform: uppercase;
  color: #000000; /* Dark red for headings */
  letter-spacing: 2px;
  font-weight: bold;
}

p, li {
  color: #000000; /* Dark red for regular text */
  font-size: 1.1rem;
  line-height: 1.6;
  margin: 15px 0;
}

/* Header Styling */
.header {
  padding: 20px;
  background-color: #ffffff; /* Dark background to match the aesthetic */
  position: relative;
}

.logo {
  width: 150px;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.logo:hover {
  transform: scale(1.1);
}

.glitch {
  font-size: 3rem;
  color: #000000;
  position: relative;
  animation: glitch 1s infinite alternate;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
}

.glitch:after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  color: #d0d0d0; /* Slight gray effect for glitch */
  text-shadow: 2px 2px 3px #000000;
  animation: glitch-2 1s infinite alternate;
}

/* Section Styles */
.section {
  padding: 40px 20px;
  background-color: #ffffff;
  margin: 20px 0;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

.section-title {
  font-size: 2.4rem;
  color: #000000; /* Dark red titles */
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.section-text {
  font-size: 1.2rem;
  color: #000000; /* Dark red text */
  line-height: 1.6;
}

/* Social Links Styling */
.social-links {
  list-style: none;
  padding: 0;
  margin-top: 20px;
}

.social-links li {
  display: inline-block;
  margin: 10px;
}

.social-links a {
  color: #000000;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.2rem;
  text-decoration: none;
  letter-spacing: 1px;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-links a:hover {
  color: #513939; /* Slight gray hover effect */
  transform: scale(1.1);
}

/* Footer Styling */
footer {
  padding: 40px 20px;
  background-color: #ffffff;
  color: #000000; /* Dark red footer text */
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
  margin-top: 30px;
  border-top: 2px solid #000000;
}

/* Glitch Effect Animations */
@keyframes glitch {
  0% { text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8); }
  20% { text-shadow: -2px -2px 5px rgba(0, 0, 0, 0.8); }
  40% { text-shadow: 2px -2px 5px rgba(0, 0, 0, 0.8); }
  60% { text-shadow: -2px 2px 5px rgba(0, 0, 0, 0.8); }
  80% { text-shadow: 2px 2px 10px rgb(0, 0, 0); }
  100% { text-shadow: -2px -2px 10px rgb(0, 0, 0); }
}

@keyframes glitch-2 {
  0% { text-shadow: -2px -2px 5px rgba(0, 0, 0, 0.8); }
  100% { text-shadow: 2px 2px 10px rgb(0, 0, 0); }
}

/* Responsive Styles for smaller screens */
@media (max-width: 768px) {
  .logo {
    width: 120px;
  }

  .section-title {
    font-size: 2rem;
  }

  .section-text {
    font-size: 1rem;
  }

  .social-links a {
    font-size: 1rem;
  }

  footer {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 20px;
  }

  .glitch {
    font-size: 2.5rem;
  }

  .section-title {
    font-size: 1.6rem;
  }

  .section-text {
    font-size: 0.9rem;
  }
}
